<template>
  <div id="updata">
    <div class="home_container">
      <div class="iconback" @click="back">
        <img src="./../../assets/back.png" alt="" />
      </div>
      <div class="home_title">
        <div class="line"></div>
        <div>雀巢咖啡，提醒每一天</div>
        <div class="line"></div>
      </div>
      <div class="title">上传游戏信息</div>
      <div class="home_form">
        <van-field v-model="userForm.gameId" label="游戏ID:" />
        <van-field
          v-model="userForm.gameValue"
          label="游戏段位:"
          @focus="show = true"
        />
        <van-action-sheet
          v-model="show"
          :actions="actions"
          @select="onSelect"
        />
        <!-- <van-dropdown-menu>
          <van-dropdown-item v-model="userForm.gameValue" :options="option1" />
        </van-dropdown-menu> -->
      </div>
      <div class="tixs">
        <p>示例:</p>
      </div>
      <div class="uploader">
        <div class="upload-bg">
          <img v-if="scanData" :src="scanData" alt="" style="width: 100%" />
          <img v-else src="./../../assets/yxsl.png" alt="" />
        </div>
        <van-uploader :after-read="afterRead">
          <div class="updatabtn">上传游戏截图</div>
        </van-uploader>
        <Vue-imageCutting
          v-if="picloalog"
          :picturePath="scanData"
          :crossDomain="crossDomainData"
          @confirm="confirmMethod"
          @cancel="cancelMethod"
          @rotate="rotateMethod"
          @restore="restoreMethod"
          @loading="loadingMethod"
          @touching="touchingMethod"
        />
      </div>

      <div class="tix">
        <p>每位ID限量只能领取一次奖励！重复领取无效</p>
      </div>
      <div class="back" style="margin-bottom: 0.2rem">
        <div @click="truebtn">确认上传</div>
      </div>
      <div class="up">
        <van-popup v-model="dialogValue">
          <div class="dialog_title">
            <span>「上传提示」</span>
          </div>
          <div class="word">
            <div class="word_left">
              <p>请到活动现场处确认</p>
              <p>凭借上传成功截图方可领奖</p>
              <p>否则无效</p>
            </div>
          </div>
          <div class="dialog_btn">
            <div class="dialog_back">
              <div @click="hcback">取消</div>
              <!-- <div @click="a">取消</div> -->
            </div>
            <div class="dialog_back">
              <div @click="goto">确定</div>
            </div>
          </div>
        </van-popup>
      </div>

      <van-loading v-if="!loadingData" type="spinner" size="24px"
        >加载中...</van-loading
      >
    </div>
  </div>
</template>

<script>
import { GetGameupdata, getAliOSSConfig, GetGameData } from "@/api/api";
import axios from "axios";
export default {
  data() {
    return {
      picturePathData: "xxx.jpg",
      crossDomainData: false,
      show: false,
      actions: [
        { name: "最强王者" },
        { name: "无双王者" },
        { name: "荣耀王者" },
      ],
      userForm: {
        gameId: "",
        gameValue: "",
      },
      dialogValue: false,
      updataimg: {
        //图片base
        cont: "",
        // 图片名字
        name: "",
        //图片地址
        lastModified: "",
      },
      picdata: {
        recognize_id: "",
        recognize_level: "",
        img_uri: "",
      },
      file: null,
      // 是否加载完
      loadingData: true,
      // 加载完成显示图
      loadimg: false,
      option1: [
        { text: "全部商品", value: 0 },
        { text: "新款商品", value: 1 },
        { text: "活动商品", value: 2 },
      ],
      //裁剪弹框
      picloalog: false,
      scanData: "",
    };
  },
  methods: {
    touchingMethod(event) {
      // console.log(event);
    },
    restoreMethod(event) {
      console.log(event);
    },
    rotateMethod(event) {
      console.log(event);
    },
    cancelMethod(event) {
      console.log(event);
    },
    loadingMethod(event) {
      console.log(event);
    },
    confirmMethod(event) {
      console.log(event);
      console.log(this.scanData);
      this.picloalog = false;
      this.scanData = event.base64;
    },
    cutDown(event) {
      console.log(event);
    },
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      // this.Toast(item.name);
      this.userForm.gameValue = item.name;
    },
    hcback() {
      this.dialogValue = false;
    },
    truebtn() {
      this.dialogValue = true;
    },
    // 返回
    back() {
      this.$router.push("/pagefive");
    },
    //上传触发事件
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      this.loadingData = false;
      console.log(file);
      this.file = file;
      // this.updataimg = file.content
      this.updataimg.lastModified = file.file.type;
      this.updataimg.cont = file.content;
      let key = "wUbxngjGDBUEsS-kulRcXh2Jp-9dXkVM";
      let secret = "gxcRJD0wqwVElf2OemeFMLWqLIo9ywNR";
      let image_base64 = file.content;
      let formData = new FormData();
      formData.append("api_key", key);
      formData.append("api_secret", secret);
      formData.append("image_base64", image_base64);
      axios({
        url: "https://api-cn.faceplusplus.com/imagepp/v2/generalocr",
        method: "post",
        data: formData,
      }).then((res) => {
        console.log(res);
        if (res.status == 200) {
          this.loadimg = this.updataimg.cont;
          this.loadingData = true;
          let data = res.data.text_info;
          console.log(data);
          this.scanData = image_base64;
          this.picloalog = true;
          let index = data[data.length - 5].line_content.indexOf("我的排名");
          if (index == 0) {
            console.log(data[data.length - 4].line_content);
            console.log(data[data.length - 1].line_content);
            let dw = data[data.length - 1].line_content;

            var name = dw;
            var reg = /[\u4e00-\u9fa5]/g;
            var names = name.match(reg);
            name = names.join("");
            console.log(name);
            //游戏段位（识别）
            this.picdata.recognize_level = name;
            //游戏id（识别）
            this.picdata.recognize_id = res.data.request_id;
          }
        }
      });
    },
    confirm() {
      this.dialogValue = false;
    },
    //上传事件
    async goto() {
      this.loadingData = false;
      if (this.userForm.gameId == "") {
        this.Toast("请输入游戏ID");
        this.loadingData = true;
        return;
      }
      if (this.userForm.gameValue == "") {
        this.Toast("请输入段位");
        this.loadingData = true;
        return;
      }
      // if (this.picdata.recognize_id == "") {
      //   this.Toast("请先上传图片");
      //   this.loadingData = true;
      //   return;
      // }
      let res = await getAliOSSConfig();
      let OSSConfig = res.data.data;
      console.log(OSSConfig);
      const form = new FormData();
      const filename = this.file.file.name;
      form.append("name", `${new Date().getTime()}${filename}`);
      form.append("key", OSSConfig.dir + filename);
      form.append("policy", OSSConfig.policy);
      form.append("OSSAccessKeyId", OSSConfig.accessid);
      // form.append("success_action_status", 200);
      form.append("callback", OSSConfig.callback);
      form.append("signature", OSSConfig.signature);
      form.append("file", this.file.file);
      console.log(this.file);
      const result = await GetGameupdata(OSSConfig.host, form).then((res) => {
        console.log(res);
        if (res.data.status == 200) {
          this.picdata.img_uri = res.data.data.filename;
        }
      });
      console.log(this.picdata.img_uri);
      GetGameData({
        game_id: this.userForm.gameId,
        game_level: this.userForm.gameValue,
        recognize_id: this.picdata.recognize_id,
        recognize_level: this.picdata.recognize_level,
        img_uri: this.picdata.img_uri,
      }).then((res) => {
        console.log(res);
        this.loadingData = true;

        if (res.data.status == 200) {
          this.dialogValue = true;
        } else {
          this.Toast(res.data.msg);
        }
      });
    },
  },
};
</script>

